body{

  /* background: url('https://picsum.photos/1080/920') no-repeat ; */
  /* background-size: cover; */
  background-color: rgb(161, 215, 218);
}
.main{
  
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 20vh;
  z-index: 2;

}
.input{
  display: flex;
  justify-content: center;
  /* align-items: center; */
  padding-top: 15vh;
}
.input input{
  padding: 5px;
  border: none;
  width: 340px;
  height: 40px;
  border-radius: 20px;
  text-align: center;
  /* background-color: rgb(212, 216, 219); */
}
.show{
  width: 60vw;
  height: 200px;

  
  /* display: flex;
  justify-content: center; */
  background: transparent;
  color: rgb(15, 15, 15);
  font-weight: 900;
}
.show h1{
  width: 100%;
  text-align: center;
  padding-top:20px ;


}

.show h3{
  width: 100%;
  text-align: center;
}

.btn{
  display: block;
  background-color: rgb(64, 79, 68);
  width: 100%;
  margin: auto;
  font-size: 18px;
  color: white;
  margin-top: 15px;
  padding: 11px;
  border:none;
  cursor: pointer;
  font-weight: bolder;
  border-radius: 10px;
}